import Vue from 'vue'
import Vuex from 'vuex'
import VueAxios from "vue-axios";
import axios from "axios";

Vue.use(VueAxios,axios);
Vue.use(Vuex)

export default new Vuex.Store({
  state:{
    items : null,
    totalExpense : 0,
    loggedUser : null,
    notificationMessage:false,
    errorMessage:false
  },
  mutations:{
      GetExpenseData(state,payload){
        state.items = payload;
      },
      calculateTotalExpense(state,payload){
        state.totalExpense = payload;
      },
      LoggedUser(state,payload){
        state.loggedUser = payload;
      },
      setNotification(state,payload){
        state.notificationMessage = payload;
      }
  },
  actions:{
       async getExpenses({ commit }) {
        console.log("1");
        var expenseData = null;
        var total = 0;
          await axios.get(`http://10.10.10.22:3010/expenses`).then((result) => {
            expenseData = result.data;
            expenseData.forEach(expense => {
            total += parseInt(expense.amount);
          })
         
          });
          commit('GetExpenseData',expenseData);
          commit('calculateTotalExpense',total);
       },
     async deleteExpense({dispatch},id){
        axios.delete(`http://10.10.10.22:3010/expenses/${id.deleteId}`).then((result)=>{
            console.log(result.data);
            dispatch('getExpenses');
        });
      },

      async AddExpense({dispatch},expenseData){
        axios.post(`http://10.10.10.22:3010/expenses`,{
                    "title":expenseData.title,
                    "amount":expenseData.amount,
                    "paidby":expenseData.paidby,
                    "files":expenseData.files
                }).then((result)=>{
                  console.log(result);
                  dispatch('getExpenses');
                })
      },

      async UserLogin({commit},formData){
       const result = await axios.get(`http://10.10.10.22:3010/users?username=${formData.username}&password=${formData.password}`);
       commit('LoggedUser',result);
       return result;
      },

      async UserRegistration({commit},RegistrationformData){
        console.log(RegistrationformData);
        const register = axios.post(`http://10.10.10.22:3010/users`,{
          "name":RegistrationformData.name,
          "email":RegistrationformData.email,
          "username":RegistrationformData.username,
          "password":RegistrationformData.password
        })
        const registerResponse = register.then((response)=>response.data)
        commit("setNotification","You are registered successfully");
        return registerResponse;
         
      }
  },
  getters : {
      getTotalExpense : (state) => state.totalExpense       
  }

})